*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
    --transition: all 500ms ease;
    scroll-behavior: smooth;
}
:root{
    --color-light: white;
  /*General Container Variables*/
  --container-width-lg: 76%;
  --container-width-md: 90%;
}

html {
    scroll-behavior: smooth;
}


body {
    line-height: 1.6;
    font-size: 0.9rem;
}

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
    max-width: 1920px;
}

section {
    padding: 10rem 0;
}
section>h2 {
    text-align: center;
}

section>p {
    text-align: center;
    margin: 0.5rem auto 4rem;
    width: 40%;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    line-height: 1.1;
}

h1 {
    font-size: 3rem;
}

h2 {
    font-size: 2rem;
}

h3 {
    font-size: 1.6rem;
}

h4 {
    font-size: 1.1rem;
}

h5 {
    font-size: 0.95rem;
}

h6 {
    font-size: 0.8rem;
}

/*General Images style*/
img {
    display: flex;
    width: 100%;
    object-fit: cover;
    overflow: hidden;
}

.btn {
    display: inline-block;
    width: fit-content;
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 0.6rem 2.5rem;
    border: 2px solid transparent;
    cursor: pointer;
}
.btn:hover {
    background-color: transparent;
    border-color: var(--color-light);
    transform: translateY(-0.5);
}


@media screen and (max-width: 1024px) {
    body {
        font-size: 1rem;
    }

    .container {
        width: var(--container-width-md);
    }

    section {
        padding: 8rem 0;
    }

    section>p {
        width: 72%;
        margin-bottom: 3rem;
    }

    h1 {
        font-size: 2rem;
    }
}

@media screen and (max-width:600px) {
    h1 {
        font-size: 2.4rem;
    }

    h2 {
        font-size: 1.8rem;
    }

    h3 {
        font-size: 1.4rem;
    }

    h4 {
        font-size: 1rem;
    }

    .btn {
        padding: 0.6rem 1.5rem;
    }

    section>p {
        width: var(--container-width-md);
    }
}